<template>
  <div>
    <!-- wrapper -->
    <div id="wrap">
      <!-- header -->
      <header id="subAdm">
        <div class="header_wrap_adm">
          <h1>위패/지방 제작</h1>
          <a @click="initClick">
            <div class="reg_btn">
              <div class="reg_btn_txt">초기화</div>
            </div>
          </a>
        </div>
      </header>
      <!-- header -->
      <!-- contents -->
      <div id="contentsAdm">
        <!-- 전체 -->
        <div class="choiceArea">
          <h1 class="instructor_title">종교 선택</h1>
          <ul class="choiceArea_inner">
            <li class="religion"><button :class="type==='normal'?'active':''" @click="makeTypeClick('normal')">일반</button></li>
            <li class="religion"><button :class="type==='chris'?'active':''" @click="makeTypeClick('chris')">기독교</button></li>
            <li class="religion"><button :class="type==='catholic'?'active':''" @click="makeTypeClick('catholic')">천주교</button></li>
            <li class="religion"><button :class="type==='bud'?'active':''" @click="makeTypeClick('bud')">불교</button></li>
          </ul>
        </div>
        <div class="choiceArea">
          <h1 class="instructor_title">카테고리 선택</h1>
          <!-- 종교일반 -->
          <Normal v-if="type==='normal'" />
          <!-- 기독교 선택시 -->
          <Chris v-if="type==='chris'" />
          <!-- 천주교 선택시 -->
          <Catholic v-if="type==='catholic'" />
          <Bud v-if="type==='bud'" />
        </div>
        <div class="choiceArea">
          <h1 class="instructor_title">글자 입력</h1>
          <div class="wordArea">
            <Button @click="makeTxtFirst('故')">故고</Button>
            <Button @click="makeTxtLast('公')">公공</Button>
            <Button @click="makeTxtLast('氏')">氏씨</Button>
            <Button @click="makeTxtLast('神位')">神位신위</Button> </div>
          <div class="inputArea"><input type="text" class="input01" v-model="contentTxt" maxlength="13">
            <div class="btnArea"><button :class="plusTxtFlag?'minusBtn':'plusBtn'" v-if="type==='normal' || type==='bud'" @click="addTxtClick">추가/삭제</button></div>
          </div>
          <div class="inputArea" v-if="plusTxtFlag"><input type="text" class="input01" v-model="contentTxt2" maxlength="13">
            <div class="btnArea"></div>
          </div>
        </div>
        <div class="choiceArea">
          <h1 class="instructor_title">글자 설정</h1>
          <div class="wordArea">
            <Button :class="fontType==='gungseo'?'active':''" @click="fontType = 'gungseo'">궁서체</Button>
            <Button :class="fontType==='nanummyeongjo'?'active':''" @click="fontType = 'nanummyeongjo'">나눔명조체</Button>
            <Button :class="fontType==='human'?'active':''" @click="fontType = 'human'">붓글씨체</Button>
          </div>
          <p>*기본서체는 궁서체로 표시되어집니다.</p>
        </div>

        
        <div v-if="surNameFlag" class="leftArea03 choiceArea">
          <h1 class="instructor_title">성씨 입력</h1>
          <div class="inputArea">
            <ul class="infoArea">
              <li class="info02" >
                <div class="NameSearchArea">
                  <input type="text" class="input04" v-model="keyword" @keyup.enter="searchSurName()"><button class="searchBtnStyle" @click="searchSurName">검색버튼</button>
                  <ul class="searchResult" v-if="getSurNameList.length>0">
                    <li :class="selectedSurNameId===surName.id?'on':''" v-bind:key="surName.id" v-for="surName in getSurNameList" @click="selectSurName(surName)">{{surName.surNameKorean}}<span class="china">{{surName.surNameMandarin}}</span></li>
                  </ul>
                </div>
              </li>
            </ul>
          </div>
        </div>
        <!--
          <div class="choiceArea">
            <h1 class="instructor_title">위패 선택</h1>
            <ul class="choiceArea_inner2">
              <li class="religion"><button>일반위패</button></li>
              <li class="religion"><button>종교위패</button></li>
            </ul>
          </div>
          -->
        <div :class="['leftArea05', 'choiceArea' , type==='chris' || type==='catholic' || type==='bud'? 'active':'']">
          <h1 class="instructor_title">문양 선택</h1>
          <ul class="patternArea">
            <li :class="['patternView', 'patternView1', type==='chris'?'active':'']"><button class="patternBtn pattern_chris01" @click="makeMarkClick('pattern_chris01')"> 기독교1</button></li>
            <li :class="['patternView', 'patternView1', type==='chris'?'active':'']"><button class="patternBtn pattern_chris02" @click="makeMarkClick('pattern_chris02')">기독교2</button></li>
            <li :class="['patternView', 'patternView2', type==='catholic'?'active':'']"><button class="patternBtn pattern_catholic01" @click="makeMarkClick('pattern_catholic01')">천주교1</button></li>
            <!--<li :class="['patternView', 'patternView2', type==='catholic'?'active':'']"><button class="patternBtn pattern_catholic02" @click="makeMarkClick('pattern_catholic02')">천주교2</button></li>-->
            <li :class="['patternView', 'patternView3', type==='bud'?'active':'']"><button class="patternBtn pattern_bud01" @click="makeMarkClick('pattern_bud01')">불교1</button></li>
            <li :class="['patternView', 'patternView3', type==='bud'?'active':'']"><button class="patternBtn pattern_bud02" @click="makeMarkClick('pattern_bud02')">불교2</button></li>
            <li :class="['patternView', 'patternView3', type==='bud'?'active':'']"><button class="patternBtn pattern_bud03" @click="makeMarkClick('pattern_bud03')">불교3</button></li>
          </ul>
        </div>
        <div class="AdmBtmArea"><button @click="saveTablet">저장하기</button><button class="prayerPreviewBtn">미리보기</button></div>
        <p>*출력은 모바일에서 "저장하기"를 누르신 후 PC에서 출력 가능합니다.</p>
      </div>
      <!-- //contents -->
      <Footer/>
    </div>
    <!-- //wrapper -->
    <div id="prayer_pop">
      <!-- 전광판 관리 팝업-->
      <div class="header_wrap">
        <h1>위패/지방<br>미리보기</h1>
        <button class="btn_close">닫기</button>
        <div class="markWrapper"  ref="printWrapId">
          <div :class="['markTop',fontType]" v-if="type!=='normal' && martkType!==''"><button :class="['patternBtn',martkType]">기독교1</button></div>
          <!--<div class="markMiddle">勸師</div>-->
          <div :class="['markBottom',fontType]">
            <ul>
              <li class="markArea">{{contentTxt}}</li>
              <li class="markArea" v-if="plusTxtFlag">{{contentTxt2}}</li>
            </ul>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Header from '@/components/common/Header'
import Footer from '@/components/common/Footer'
import Normal from '@/components/tablet/Normal'
import Chris from '@/components/tablet/Chris'
import Bud from '@/components/tablet/Bud'
import Catholic from '@/components/tablet/Catholic'

import myMixin from '@/mixin/mixin'

import { mapGetters } from 'vuex'
import { eventBus } from '@/main'

export default {
  name: 'ObituaryReg',
  mixins: [myMixin],
  components: {
    Header,
    Footer,
    Normal,
    Bud,
    Catholic,
    Chris
  },
  props: {},
  data() {
    return {
      fontType:'gungseo',
      type: 'normal',
      martkType: '',
      categoriFlag: 0,
      manFlag: false,
      plusTxtFlag: false,
      contentTxt: '',
      contentTxt2: '',
      originalContext:'',
      changeFlag:false,
      surNameFlag:false,
      keyword:'',
      selectedSurNameId:null,

      subjectTxt:'',
      selectedIndex:0,
    }
  },
  computed: {
    ...mapGetters(['getSurNameList']),
    typeTxt() {
      if (this.type === 'normal') {
        return '일반'
      }
      if (this.type === 'catholic') {
        return '천주교'
      }
      if (this.type === 'bud') {
        return '불교'
      }
      if (this.type === 'chris') {
        return '기독교'
      }
      return ''
    }
  },
  mounted() {
    const context = this
		eventBus.$on('makeTabletTxt', (val) => {
            context.originalContext = val.contentTxt
            context.contentTxt = val.contentTxt
            context.surNameFlag = val.surNameFlag

            context.subjectTxt = val.subjectTxt
            context.selectedIndex = val.selectedIndex
            
            if(!context.surNameFlag){
                context.$store.commit('SUR_NAME_INIT')
                context.keyword = ''
            }
		});
  },
  beforeDestroy() {
    eventBus.$off('makeTabletTxt')
    this.$store.commit('SUR_NAME_INIT')
  },
  methods: {
        selectSurName(surName) {
          
          //surNameKorean
          //surNameMandarin
          if(this.surNameFlag){
            this.selectedSurNameId = surName.id
             this.contentTxt = this.originalContext.replace('ㅇㅇㅇ',surName.surNameMandarin) 
          }
    },
    searchSurName(){
        
      const parameter = {
        keyword: this.keyword
      }
      this.$store.dispatch('REQUEST_SUR_NAME_LIST', parameter)
    },
    initClick() {
      this.type = 'normal'
      this.martkType = ''
      this.categoriFlag = 0
      this.manFlag = false
      this.plusTxtFlag = false
      this.contentTxt = ''
      this.contentTxt2 = ''
      this.subjectTxt = ''
      this.selectedIndex = 0
      eventBus.tabletInit()
    },
    makeMarkClick(type) {
      this.martkType = type
    },
    makeTypeClick(type) {
      this.contentTxt = ''
      this.contentTxt2 = ''
      this.plusTxtFlag = false
      this.manFlag = false
      this.type = type
      this.surNameFlag = false
      if(!this.surNameFlag){
        this.$store.commit('SUR_NAME_INIT')
        this.keyword = ''
      }
      if (this.type === 'normal') {
        this.martkType = ''
      }
      if (this.type === 'catholic') {
        this.martkType = 'pattern_catholic01'
      }
      if (this.type === 'bud') {
        this.martkType = 'pattern_bud01'
      }
      if (this.type === 'chris') {
        this.martkType = 'pattern_chris01'
      }
    },
    addTxtClick() {
      this.plusTxtFlag = !this.plusTxtFlag
      if (!this.plusTxtFlag) {
        this.contentTxt2 = ''
      }
    },
    makeTxtFirst(txt) {
      if (this.contentTxt.indexOf(txt) !== 0) {
        if (this.contentTxt.length < 13) {
          this.contentTxt = txt + this.contentTxt
        }
      }
    },
    makeTxtLast(txt) {
      if (this.contentTxt.length < 13) {
        this.contentTxt = this.contentTxt + txt
      }
    },
    saveTablet() {
      
       //기독교 불교
      const contentTxt = this.$refs.printWrapId.innerHTML

      
      
      const parameter = {
        tablet: {
          tabletReligionType: this.type,
          tabletMartkType: this.martkType,
          tabletSubjectTxt: this.subjectTxt,
          tabletSelectedIndex: this.selectedIndex,
          tabletTitle: this.contentTxt,
          tabletContentTxt: this.contentTxt,
          tabletContentTxt2:this.contentTxt2,
          tabletType: { codeId: 'TABLET-0' }
        }
      }
      this.$store.dispatch("REGIST_TABLET", parameter);
    }
  }
}
</script>

