<script>
import { mapGetters } from 'vuex'
import { eventBus } from '@/main'

import myMixin from '@/mixin/mixin'

export default {
  name: 'Normal',
  mixins: [myMixin],
  components: {},
  props: {},
  data() {
    return {
      categoriFlag: 0,
      manFlag: false,
      plusTxtFlag: false,
      subjectTxt: '學生',
      familyTxt1: '',
      familyTxt2: '',
      contentTxt: '',
      contentTxt2: '',
      categoryTxtValue: null,
      surNameFlag: false,
      selectedIndex:0
    }
  },
  computed: {
    ...mapGetters([]),
  },
  mounted() {
    const context = this
    eventBus.$on('tabletInit', () => {
      context.categoriFlag = 0
      context.manFlag = false
      context.plusTxtFlag = false
      context.subjectTxt = '學生'
      context.familyTxt1 = ''
      context.familyTxt2 = ''
      context.contentTxt = ''
      context.contentTxt2 = ''
    });
  },
  beforeDestroy() {
    eventBus.$off('tabletInit')
  },
  methods: {
    categoryChange(categoryTxt,e) {
      
      this.selectedIndex = e.target.selectedIndex+1
      const category = categoryTxt.split(',')
      this.makeTxt(category[0], category[1], category[2], category[3], category[4])
    },
    makeTxt(txt1, txt2, clickCount, manFlag,surNameFlag) {
      this.familyTxt1 = txt1
      this.familyTxt2 = txt2
      this.manFlag = manFlag === 'true'
      this.categoriFlag = clickCount
			this.surNameFlag =  surNameFlag === 'true' 
      if (this.manFlag) {
        this.contentTxt = this.familyTxt1 + this.subjectTxt + this.familyTxt2
      } else {
        this.contentTxt = this.familyTxt1 + this.familyTxt2
      }
    },
    makeSubjectTxt(txt) {
      this.subjectTxt = txt
      if (this.manFlag) {
        this.contentTxt = this.familyTxt1 + this.subjectTxt + this.familyTxt2
      } else {
        this.contentTxt = this.familyTxt1 + this.familyTxt2
      }
    },
  },
  updated() {
		const parameter={
			contentTxt: this.contentTxt,
      surNameFlag: this.surNameFlag,
      subjectTxt: this.subjectTxt,
      selectedIndex: this.selectedIndex
		}
		eventBus.makeTabletTxt(parameter)
  }
}
</script>