<template>
  <div>
    <div class="catArea catArea-first">
      <!-- 천주교 선택시 -->
      <select class="choiceSelect sam2" @change="categoryChange(categoryTxtValue,$event)" v-model="categoryTxtValue">
        <option value="聖徒,,1,false">성도/聖徒</option>
        <option value="敎友,,2,false">교우/敎友</option>
        <option value="使徒,,3,false">사도/使徒</option>
      </select>
      <!-- <div class="instructor_title_rightArea"><input type="checkbox"><span class="marginRight20">직분 가로/세로 전환</span> <input type="checkbox">직분 한글로 입력</div> -->
    </div>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'
import { eventBus } from '@/main'

import myMixin from '@/mixin/mixin'

export default {
  name: 'Normal',
  mixins: [myMixin],
  components: {},
  props: {},
  data() {
    return {
      categoriFlag: 0,
      manFlag: false,
      plusTxtFlag: false,
      subjectTxt: '學生',
      familyTxt1: '',
      familyTxt2: '',
      contentTxt: '',
      contentTxt2: '',
      categoryTxtValue: null,
      selectedIndex:0
    }
  },
  computed: {
    ...mapGetters([]),
  },
  mounted() {
    const context = this
    eventBus.$on('tabletInit', () => {
      context.categoriFlag = 0
      context.manFlag = false
      context.plusTxtFlag = false
      context.subjectTxt = '學生'
      context.familyTxt1 = ''
      context.familyTxt2 = ''
      context.contentTxt = ''
      context.contentTxt2 = ''
    });
  },
  beforeDestroy() {
    eventBus.$off('tabletInit')
  },
  methods: {
    categoryChange(categoryTxt,e) {
      this.selectedIndex = e.target.selectedIndex+1
      const category = categoryTxt.split(',')
      this.makeTxt(category[0], category[1], category[2], category[3])
    },
    makeTxt(txt1, txt2, clickCount, manFlag) {
      this.familyTxt1 = txt1
      this.familyTxt2 = txt2
      this.manFlag = manFlag === 'true'
      this.categoriFlag = clickCount
      if (this.manFlag) {
        this.contentTxt = this.familyTxt1 + this.subjectTxt + this.familyTxt2
      } else {
        this.contentTxt = this.familyTxt1 + this.familyTxt2
      }
    },
  },
  updated() {
		const parameter={
			contentTxt: this.contentTxt,
			surNameFlag: false,
      subjectTxt: this.subjectTxt,
      selectedIndex: this.selectedIndex
		}
		eventBus.makeTabletTxt(parameter)
  }
}
</script>